body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #ccc;
}

main {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.table-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    /* padding: 4px; */
}

.header {
    font-size: 1.5rem;
}

.table {
    margin-top: 1rem;
    width: 98%;
    border-spacing: 2px 2px;
}

.table tr {
    margin: 2px;
}

input {
    border: none;
}

input:focus {
    opacity: .8;
}

.table td {
    max-width: 19vw;
    height: 4vh;
    padding: 3px 0px;
    border-radius: 5px;
    /* overflow: hidden; */
}

.table input {
    background-color: rgb(200, 234, 245);
    text-align: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    padding: 3px 0px 2px;
}

.table td:first-child {
    width: 20px;
    height: 100%;
    margin: 0;
    font-size: 1.1rem;
    user-select: none;
}

.table td:last-child {
    padding: 0;
}

table .players .player {
    /* width: calc(50px + .5vmin); */
    border-radius: 5px;
    /* overflow: hidden; */
    padding: 4px 0px;
}

.players input {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  width: 100%;
  height: 100%;
  background-color: rgb(238, 238, 245);
  border: none;
  text-align: center;
  border-radius: 5px;
  font-size: 1em;
  overflow: hidden;
  padding: 3px 0px 3px 0px;
}

.players .first {
    /* background-color: rgb(145, 211, 46); */
    background-color: rgb(73, 248, 73);
}

/* .players .second {
    background-color: rgb(165, 165, 223);
}

.players .third {
    background-color: orange;
} */

.players .fourth {
    background-color: rgb(250, 0, 0);
    color: white;
}


.players td:nth-child(1) {
    opacity: 0;  
    background-color: none;  
}

.players td:last-child {
    opacity: 0;  
    background-color: none;
}

/* .table td:nth-child(2) {
    background-color: rgb(189, 192, 230);
}

.table td:nth-child(3) {
    background-color: rgb(187, 224, 196);
}

.table td:nth-child(4) {
    background-color: rgb(224, 187, 216);
}

.table td:nth-child(5) {
    background-color: rgb(124, 207, 117);
} */

.table td:last-child {
    width: 15px;
    padding: 1px;
    border-radius: 5px;
    /* padding-left: 0px; */
}


button {
    text-decoration: none;
    border-radius: 4px;
    padding: 6px 8px;
    color: white;
    box-shadow: 1px 2px 3px #ccc;
    font-size: 1.4rem;
    font-weight: 300;
    cursor: pointer;
    transition: background-color .2s linear;
}

.total-scores .total-score {
    border-radius: 5px;
    padding: 6px 4px;
    background-color: rgb(28, 90, 170);
    /* border: 1px solid rgb(28, 90, 170); */
    color: white;
    font-size: 1.2em;
}

.table .total-scores td:nth-child(1) {
    background-color: rgb(250, 250, 249);
    /* color: rgb(28, 90, 170); */
    color: rgb(202, 164, 11);
    border: none;
    width: 15px;
    padding: 8px 2px 0px;
}

.total-scores td:last-child {
    display: none;
}

.curr-hand_player-score td {
    padding: 0px;
    
}

.curr-hand_player-score td:last-child {
    padding: 0;
}

.curr-hand_player-score input {
    background-color: rgb(154, 184, 237);
    font-size: 1em;
    width: 100%;
}

#suit {
    height: fit-content;
    width: calc(15px + .5vmin);
}

#select-suit {
    border-radius: 5px;
    background-color: white;
    width: 100%;
    /* height: 100%; */
    font-size: 1em;
    text-decoration: none;
    text-align: center;
    outline: none;
    appearance: none;
    padding: 4px 0px 4px 2px;
    margin: 0;
    cursor: pointer;
    /* font-family: 'FontAwesome'; */
    /* font-weight: 900; */
    display: flex;
    align-items: center;
    justify-content: center;
}

#select-suit:hover {
    background-color: rgb(248, 243, 232);
    cursor: pointer;
}

/* #select-suit option {
    font-size: 2rem;
    padding-left: 4px;
} */



#choose {
    color: black;
}

#diamonds {
    color: red;
}

#hearts {
    color: red;
}

#spades {
    color: black;
}

#clubs {
    color: black;
}

.buttons {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.buttons .btn {
    margin: 0 4px 5px;
}

#gosterge {
    padding-top: 12px;
}

#gosterge-g-harfi {
    border: 1px solid green;
    border-radius: 50%;
    padding: 2px 6px;
    font-size: .9em;
}

#penalty {
    padding-top: 14px;
}

#penalty-c-harfi {
    border: 1px solid red;
    border-radius: 50%;
    padding: 4px 9px;
}

.indicators {
    border: 1px solid green;
}

.indicators  td {
    /* height: calc(20px + .5vmin); */
    padding-top: calc(14px + .5vmin);
}

.indicators td:last-child {
    height: 98%;
    padding: 0px;
}

.indicators input[type=radio] {
    /* font-size: 1.2rem; */
    /* line-height: 0.5rem; */
    background-color: aquamarine;
    cursor: pointer;
}

.penalties {
    padding-top: 12px;
}

.penalties td {
    padding-top: 12px;
}

.add-scores td:last-child {
    padding: 0;
}

.btn-add-score-row {
    width: 8vw;
    height: 96%;
    padding: 4px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: rgb(76, 160, 50);
    border: 1px solid rgb(55, 133, 31);
    box-shadow: 1px 2px 3px #ccc;
    transition: background-color .2s linear;
}

.btn-add-score-row:hover {
    background-color: rgb(57, 138, 32);
}

.btn-new-row {
    margin-top: 25px;
    padding: 6px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    background-color: rgb(76, 160, 50);
    border: 1px solid rgb(55, 133, 31);
    box-shadow: 1px 2px 3px #ccc;
    color: white;
    font-size: .8em;
    font-weight: 300;
    transition: background-color .2s linear;
}


.btn-new-row:hover {
    background-color: rgb(57, 138, 32);
}

.btn-clear-all {
    margin-top: 25px;
    padding: 6px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    background-color: rgb(223, 53, 53);
    border: 1px solid rgb(201, 50, 75);
    box-shadow: 1px 2px 3px #ccc;
    color: white;
    font-size: .8em;
    font-weight: 300;
    transition: background-color .2s linear;
}

.btn-clear-all:hover {
    background-color: rgb(201, 50, 75);
}

.btn-clear-all-scores {
    margin-top: 25px;
    padding: 6px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    background-color: rgb(190, 27, 27);
    border: 1px solid rgb(156, 13, 37);
    box-shadow: 1px 2px 3px #ccc;
    color: white;
    font-size: .8em;
    font-weight: 300;
    transition: background-color .2s linear;
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-clear-all-scores:hover {
    background-color: rgb(156, 13, 37);
}

.btn-clear-curr-indicators, .btn-clear-curr-penalties {
    border-radius: 5px;
    cursor: pointer;
    width: 8vw;
    height: 100%;
    border: none;
    background-color: rgb(235, 65, 65);
    /* background-color: white; */
    /* color: rgb(0, 132, 255); */
    color: white;
    /* color: black; */
    padding: 10px 0px;
    margin-top: 6px; 
    /* border: 1px solid rgb(182, 95, 95); */
}

.btn-clear-curr-penalties {
    height: 75%;
    /* margin-top: 8px; */
}

.btn-clear-curr-penalties:hover, .btn-clear-curr-indicators:hover {
    background-color: rgb(179, 39, 39);
}

.penalties td:last-child {
    padding-top: 4px;
}

