.App {
  text-align: center;
  background-color: rgb(250, 250, 249);
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #282c34;
  /* background-color: #232c3d; */
  min-height: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + .5vmin);
  color: white;
}

.App-h1 {
  color: #61dafb;
  padding: 0px;
  margin: 10px;
}

.main-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100vw;
  padding: 0;
  margin: 0;

}
