.leader-board {
  margin-top: 40px;
  width: 99%;
  overflow: auto;
  font-size: 1.1rem;
  margin-left: 3px;
}

.score-board {
  /* display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column; */
  width: 98%;
  text-align: left;
  margin-bottom: 40px;
  /* border: 1px solid rgb(128, 128, 128); */
  /* border-radius: 5px; */
}

.headings {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: rgb(87, 85, 85);
  margin-bottom: 5px;
}

.places {
  margin-right: 50px;
  text-decoration: underline;
}

.cross-differences {
  margin-left: 70px;
  margin-right: 20px;
  text-decoration: underline;
}

#board-row-1 td {
  background-color: rgb(73, 248, 73);
}

#board-row-2 td {
  background-color: rgb(124, 227, 250);
}

#board-row-3 td {
  background-color: rgb(255, 255, 53);
}

#board-row-4 td {
  background-color: rgb(255, 124, 124);
}

#board-row-1 td:nth-child(5),
#board-row-1 td:nth-child(6),
#board-row-1 td:nth-child(7) {
  background-color: rgb(250, 250, 249);
  padding-left: 0;
  padding-right: 0;
  width: 5%;
}

#board-row-2 td:nth-child(5),
#board-row-2 td:nth-child(6),
#board-row-2 td:nth-child(7) {
  background-color: rgb(250, 250, 249);
  padding-left: 0;
  padding-right: 0;
  width: 5%;
}

#board-row-3 td:nth-child(5),
#board-row-3 td:nth-child(6),
#board-row-3 td:nth-child(7) {
  background-color: rgb(250, 250, 249);
  padding-left: 0;
  padding-right: 0;
  width: 5%;
}

#board-row-4 td:nth-child(5),
#board-row-4 td:nth-child(6),
#board-row-4 td:nth-child(7) {
  background-color: rgb(250, 250, 249);
  padding-left: 0;
  padding-right: 0;
  width: 5%;
}

.score-board td {
  padding: 4px 8px;
  /* border-spacing: 0; */
  user-select: none;
}

.score-board td:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  width: 2%;
  text-align: center;
}

.score-board .board-row td:nth-child(2) {
  width: 15%;
}

.score-board .board-row td:nth-child(3) {
  width: 5%;
  text-align: right;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

#board-row-1 .chevron,
#board-row-2 .chevron,
#board-row-3 .chevron,
#board-row-4 .chevron {
  width: 1%;
  text-align: center;
  padding-top: 4px;
  padding-left: 2px;
  padding-right: 2px;
  background-color: rgb(250, 250, 249);
  color: red;
}

.score-board .board-row td:nth-child(5) {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.fa-chevron-down {
  margin-top: 6px;
}

