}

.difference {
  background-color: rgb(207, 202, 253);
  padding: 2px 3px 2px 1px;
  border-radius: 5px;
}

.score-board .opponent-differences {
  background-color: rgb(250, 250, 249);
}


#board-row-1 .opponent {
  background-color: greenyellow;
