.score-row td .hand {
  background-color: rgb(156, 230, 205);
  border-radius: 5px;
  padding: 3px;

}

.btn-delete-row {
    border-radius: 5px;
    cursor: pointer;
    width: 8vw;
    height: 98%;
    border: none;
    background-color: rgb(221, 100, 100);
}

.btn-delete-row:hover {
    background-color: rgb(187, 47, 47);
}

/* Chrome, Safari, Edge, Opera */
.score-row td,
.score-row td input {
  -webkit-appearance: none;
  /* margin: 4px; */
  /* height: 4vh; */
}

.score-row td:first-child {
  width: 15px;
}

.score-row td:last-child {
  border-radius: 5px;
  cursor: pointer;
  width: 25px;
  border: none;
  /* padding: 0px; */
  /* margin-top: 6px; */
}

/* Firefox */
.score-row td input[type=number] {
  -moz-appearance: textfield;
}